
// Шрифты ====================================================================

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Light.eot');
	src: local('☺'), url('../fonts/ProximaNova-Light.woff') format('woff'), url('../fonts/ProximaNova-Light.ttf') format('truetype'), url('../fonts/ProximaNova-Light.svg') format('svg');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Semibold.eot');
	src: local('☺'), url('../fonts/ProximaNova-Semibold.woff') format('woff'), url('../fonts/ProximaNova-Semibold.ttf') format('truetype'), url('../fonts/ProximaNova-Semibold.svg') format('svg');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Bold.eot');
	src: local('☺'), url('../fonts/ProximaNova-Bold.woff') format('woff'), url('../fonts/ProximaNova-Bold.ttf') format('truetype'), url('../fonts/ProximaNova-Bold.svg') format('svg');
	font-weight: 700;
	font-style: normal;
}



// ===========================================================================


body {

}

.wrapper {
	overflow: hidden;
	min-height: 100%;
	text-transform: uppercase;
}

.container {
	max-width: 1700px;
	padding: 0 100px;
	margin: 0 auto;

	@media (max-width: 1000px) {
		padding: 0 50px;
	}
	@media (max-width: 700px) {
		padding: 0 35px;
	}
}



// ===========================================================================




/* ==========================================================================
HEADER
========================================================================== */

.header {
	position: absolute;
	width: 100%;
	z-index: 50;
	left: 0;
	top: 40px;
	&__row {
		@include flexbox();
		justify-content: space-between;
		align-items: center;
		height: 90px;
		margin: 0 0 16.4%;
	}

	&__menu {}

	&__logo {
		z-index: 2;
		img {
			max-width: 100%;
		}
	}

	&__text {
		text-align: center;
	}

	&__title {}

	&__subtitle {}

	@media (max-width: 700px) {
		&__row {
			margin: 0 0 30.4%;
		}
	}

}

.menu {
	&__icon {}

	&__body {}

	&__list {
		@include flexbox();
		li {
			position: relative;
			padding: 0 36px;
			&:first-child{padding: 0 35px 0 0;}
			&:last-child{padding: 0 0 0 35px;}
		}
	}

	&__link {
		font-size: 18px;
		font-weight: 600;
		letter-spacing: 0.29em;
		color: #ffffff;
		&:hover{
			color: #f84545;
			transition: 0.5s;
		}
	}

	@media (max-width: 1170px) {
		&__list {
			li {
				position: relative;
				padding: 0 15px;
				&:first-child{padding: 0 15px 0 30px;}
				&:last-child{padding: 0 0 0 15px;}
			}
		}
	}

	@media (max-width: 767px) {
		&__body {
			position: fixed;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			overflow: auto;
			background-color: #4d4959;
			padding: 170px 0 20px 0;
			@include tr(0px, -100%, 0px);
			@include anim(0.3s,0s);
			&.active{
				@include tr(0px,0px,0px);
			}
		}
		&__list {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			li {
				padding: 0 0 45px 0;
				&:first-child {
					padding: 0 0 45px 0;
				}
				&:last-child {
					padding: 0;
				}
			}
		}
		&__link {
			color: #fff;
			font-size: 20px;
		}
	}
}

.title {
	@include fluidFontSize(30px, 60px, 320px, 1700px, 18px);
	line-height: 8vh;
	font-weight: 300;
	letter-spacing: 2.1vw;
	color: #ffffff;
	margin: 0 0 2.2%;
	@media (max-width: 480px) {
		line-height: 6vh;
	}
}

.subtitle {
	@include fluidFontSize(20px, 30px, 320px, 1700px, 18px);
	font-weight: 700;
	letter-spacing: .7vw;
	color: #f84545;
}




.icon-menu {
	@media (max-width: 767px) {
		display: block;
		position: absolute;
		top: 33px;
		right: 15px;
		width: 30px;
		height: 18px;
		cursor: pointer;
		z-index: 5;
		span {@include anim(0.3s,0s);top: 8px; left: 0; position: absolute; width: 100%; height: 2px; background-color: #fff;
			&:first-child {top: 0;}
			&:last-child {top: auto; bottom: 0;}
		}
		&.active{
			span {@include scale(0);background-color: #fff;
				&:first-child{@include rotate(45deg);top: 8px;}
				&:last-child{@include rotate(-45deg);bottom: 8px;}
			}
		}
	}

}

@media (max-width: 767px) {
	body.lock {
		overflow: hidden;
	}
}




/* ==========================================================================
Main-screen
========================================================================== */

.page {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	@include flexbox();
	flex-direction: column;
}
.main-screen {
	flex: 1 0 100%;
	position: relative;
	margin: 0 0 4.95%;
	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}

.arrow {
	position: absolute;
	bottom: 45px;
	left: 50%;
	margin: 0 0 0 -42.5px;
}



/* ==========================================================================
CONTENT
========================================================================== */

.content{

}




/* ==========================================================================
SHOP
========================================================================== */

.shop {
	margin: 0 0 5.45%;

	&__row {
		@include flexbox();
		margin: 0px -28.5px 3.5%;
	}

	&__column {
		flex: 0 1 33.333%;
		@include flexbox();
		flex-direction: column;
		padding: 0 1.8%;
		height: auto;
		justify-content: space-between;

		&_b {
			flex: 0 1 66.667%;

			.item {
				margin: 0;

				&__image {}

				&__logo {
					left: 5.7%;
					bottom: 6%;
					img {
						margin: 0 0 4.4%;
					}
				}

				&__title {
					@include fluidFontSize(25px, 60px, 320px, 1700px, 60px);
					letter-spacing: 1.4vw;
					z-index: 2;
					font-weight: 300;
					width: 100%;
				}
			}
		}

		&_l {
			.item {
				padding: 0 0 125.5%;
				margin: 0;

				&__image {}

				&__logo {
					bottom: 5.5%;
					left: 8.5%;
				}
				&__title {
					@include fluidFontSize(15px, 40px, 320px, 1700px, 40px);
				}
			}
		}
	}

	&__item {
		&_first {
			margin: 0 0 12%;
		}
	}

	@media (max-width: 700px) {
		&__row {
			flex-wrap: wrap;
		}
		&__column {
			&_s {
				flex: 0 1 100%;
				flex-direction: row;
			}
			&_b {
				flex: 0 1 100%;
				margin: 0 0 3.5%;
			}
			&_l {
				.item {
					padding: 0;
				}
			}
		}
		&__item {
			&_first {
				margin: 0 3.6% 0 0;
			}
		}
	}
}


.item {
	display: block;
	height: 100%;
	position: relative;
	padding: 0 0 77.764%;
	overflow: hidden;

	&__image {
		position: absolute;
		img {
			max-width: 100%;
			height: auto;
			-webkit-transition: all 0.8s linear;
			-moz-transition: all 0.8s linear;
			transition: all 0.8s linear;
			&:hover {
				-moz-transform: scale(1.1);
				-ms-transform: scale(1.1);
				transform: scale(1.1);
			}
		}
	}

	&__logo {
		position: absolute;
		left: 9%;
		bottom: 8%;
		color: #ffffff;
		z-index: 2;
		opacity: 1;
		visibility: visible;
		&_first {
			img {width: 11.358vw;}
		}
		&_second {
			img {width: 4.72vw;}
		}
		&_third {
			img {width: 8.155vw;}
		}
		&_fourth {
			img {width: 16.017vw;}
		}
		&_fifth {
			img {width: 6.407vw;}
		}
		&_sixth {
			img {width: 10.659vw;}
		}
		img {
			margin: 0 0 8%;
			max-width: 100%;
			max-height: 100%;
		}
	}

	&__title {
		@include fluidFontSize(15px, 40px, 320px, 1700px, 40px);
		position: relative;
		font-weight: 300;
		line-height: 110%;
		letter-spacing: 0.85vw;
		color: #ffffff;
		z-index: 2;
		width: 80%;

		&_wide {
			width: 100%;
		}
	}

	@media (max-width: 700px) {
		padding: 0;
		&__image {
			position: relative;
		}
		&__logo {
			&_first {
				img {width: 22.716vw;}
			}
			&_second {
				img {width: 9.44vw;}
			}
			&_third {
				img {width: 16.31vw;}
			}
			&_fourth {
				img {width: 32.034vw;}
			}
			&_fifth {
				img {width: 12.814vw;}
			}
			&_sixth {
				img {width: 21.318vw;}
			}
		}
	}

}


/* ==========================================================================
GALLERY
========================================================================== */

.gallery {
	position: relative;

	&__title {
		position: absolute;
		left: 2.2%;
		right: 0;
		top: 0;
		bottom: 1%;
		@include flexbox();
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
		text-align: center;
		max-width: 1370px;
		z-index: 2;
	}

	&__block {
		@include flexbox();
		flex-wrap: wrap;
		position: relative;
	}

	&__image {
		flex: 1 0 25%;
		height: auto;
		padding: 0 0 25%;
	}

}




/* ==========================================================================
FOOTER
========================================================================== */

.footer {
	background-color: #ffffff;

	&__row {
		@include flexbox();
		padding: 60px 0;
		justify-content: space-between;
		align-items: center;
	}

	&__nav {
		@include flexbox();
		flex: 0 1 66.25%;
		justify-content: space-between;

		li{
			list-style-type: none;
			padding: 0 34px;
			&:first-child {padding: 0 34px 0 0;}
			&:last-child {padding: 0 0 0 34px;}

			a {
				font-size: 18px;
				font-weight: 700;
				letter-spacing: 6px;
				color: #1a1a1a;
				margin-right: -4px;
				&:hover {
					color: $main-color;
					transition: 0.5s;
				}
			}
		}
	}


	&__social {}

	@media (max-width: 1380px) {
		&__nav {
			flex: 0 1 80%;

			li {
				padding: 0 17px;

				&:first-child {
					padding: 0 17px 0 0;
				}

				&:last-child {
					padding: 0 0 0 17px;
				}
			}
		}
	}

	@media (max-width: 1170px) {
		&__row {
			padding: 20px 0;
		}
		&__nav {
			flex-wrap: wrap;
			justify-content: flex-start;

			li {
				padding: 0 25px;
				margin: 0 0 15px 0;

				&:first-child {
					padding: 0 25px 0 0;
				}

				&:last-child {
					padding: 0 0 0 25px;
				}
			}
		}
	}

	@media (max-width: 670px) {
		&__row {
			flex-direction: column;
		}
		&__nav {
			flex-direction: column;
			align-items: center;
			margin: 0 0 20px 0;
			li {
				padding: 0;

				&:first-child, &:last-child {
					padding: 0;
				}
			}
		}
	}

}

.social {
	flex: 0 1 9.8%;
	@include flexbox();
	justify-content: space-between;
	align-items: center;

	@media (max-width: 1170px) {
		flex: 0 1 20%;
	}

	&__link {

		&:first-child {padding: 0 20px 0 0;}
		&:last-child {padding: 0 0 0 20px;}
		i {
			font-size: 28px;
			color: #1a1a1a;

			&:hover {
				color: $main-color;
				transition: 0.5s;
			}

		}
	}
}



// ==========================================================================

.ibg {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	img {display: none;}
}
